.form {
  background: #f9f9f9;
  padding: 60px 30px;
  text-align: center;
}

.form h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  color: #333;
}

.formc {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
}

.box {
  flex: 1;
  min-width: 300px;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.box input,
.box textarea {
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}

.box textarea {
  height: 120px;
  resize: vertical;
}

.box button {
  background: #a36bb0;
  color: white;
  border: none;
  padding: 12px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
}

.box button:hover {
  background: #8f58a0;
}

.details {
  flex: 1;
  min-width: 250px;
  max-width: 400px;
  text-align: left;
}

.details h3 {
  font-size: 1.2rem;
  margin-top: 20px;
  color: #a36bb0;
}

.details p {
  font-size: 0.95rem;
  color: #444;
  margin-bottom: 8px;
}

.social{
  display: flex;
  gap: 15px;
  font-size: 1.5rem;
  margin-top: 10px;
}
.social img {
  width: 30px; 
  height: 30px;  
  object-fit: contain; /* keeps aspect ratio inside the box */
}
