.whyus {
  background: #fff;
  padding: 60px 30px;
  text-align: center;
}

.whyus h2 {
  font-size: 2rem;
  margin-bottom: 10px;
  color: #333;
}

.whyus p {
  font-size: 1rem;
  margin-bottom: 40px;
  color: #666;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.whyus-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}

.whyus-card {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  max-width: 280px;
  text-align: left;
  transition: transform 0.3s ease;
}

.whyus-card:hover {
  transform: translateY(-5px);
}

.icon {
  font-size: 2rem;
  margin-bottom: 10px;
}

.whyus-card h3 {
  font-size: 1.2rem;
  color: #a36bb0;
  margin-bottom: 10px;
}

.whyus-card p {
  font-size: 0.95rem;
  color: #444;
}