.partner {
  background: #f5f5f5;
  padding: 60px 20px;
  text-align: center;
  overflow: hidden;
}

.partner h2 {
  font-size: 2rem;
  margin-bottom: 10px;
  color: #333;
}

.partner p {
  font-size: 1rem;
  margin-bottom: 30px;
  color: #666;
}

.partner-slider {
  overflow: hidden;
  width: 100%;
  position: relative;
}

.partner-track {
  display: flex;
  width: max-content;
  animation: scroll-left 20s linear infinite;
}

.partner-card {
  flex: 0 0 auto;
  background: #a36bb0;
  color: white;
  padding: 12px 24px;
  margin: 0 10px;
  border-radius: 6px;
  font-weight: 500;
  font-size: 1rem;
  white-space: nowrap;
}

/* Keyframe for right-to-left scroll */
@keyframes scroll-left {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}